import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CustomInput,
  FormGroup,
  InputGroup,
  InputGroupAddon,
  Label,
  Spinner,
} from 'reactstrap';
import { Separator } from '../../../../components/common/CustomBootstrap';
import IntlMessages from '../../../../helpers/IntlMessages';

const UploadResume = ({ editResume, toggleEditResume }) => {
  const [loading, setLoading] = useState(false);
  const handleUpload = () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      toggleEditResume(!editResume);
    }, 1400);
  };

  return (
    <Card className="mb-4">
      <div className="position-absolute card-top-buttons">
        <Button
          outline
          color="primary"
          onClick={() => {
            toggleEditResume(!editResume);
          }}
          className="icon-button"
        >
          <i className="simple-icon-pencil" />
        </Button>
      </div>
      <CardBody>
        <CardTitle>
          <h2 className="lead">
            <IntlMessages id="user.upload-resume" />
          </h2>
        </CardTitle>

        <Separator className="mb-3" />

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.upload-resume" />
          </Label>
          {loading ? (
            <div className="mb-3 text-center">
              <Spinner color="primary" className="mb-1" />
            </div>
          ) : (
            <InputGroup>
              <CustomInput
                type="file"
                id="resume"
                name="resume"
                disabled={!editResume}
              />
              <InputGroupAddon addonType="append">
                <Button
                  color="secondary"
                  onClick={handleUpload}
                  disabled={!editResume}
                >
                  <IntlMessages id="forms.upload-button" />
                </Button>
              </InputGroupAddon>
            </InputGroup>
          )}
        </FormGroup>
      </CardBody>
    </Card>
  );
};

export default UploadResume;
