/* eslint-disable camelcase */
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CustomInput,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import IntlMessages from '../../../../helpers/IntlMessages';

const handleSuccessButtonClick = () => {
  return new Promise((success) => {
    setTimeout(() => {
      success('Everything went right!');
    }, 1400);
  });
};

const WorkExperience = ({
  editWorkExperience,
  toggleEditWorkExperence,
  profileData,
}) => {
  const { experiences } = profileData;
  return (
    <Card className="mb-4">
      <div className="position-absolute card-top-buttons">
        <Button
          outline
          color="primary"
          onClick={() => {
            toggleEditWorkExperence(!editWorkExperience);
          }}
          className="icon-button"
        >
          <i className="simple-icon-pencil" />
        </Button>
      </div>
      <CardBody>
        <CardTitle>
          <h2 className="lead">
            <IntlMessages id="forms.work-experience" />
          </h2>
        </CardTitle>

        <Separator className="mb-3" />

        <FormGroup>
          <CustomInput
            type="checkbox"
            id="formal-education"
            disabled={!editWorkExperience}
            label="I am yet to find opportunity."
          />
        </FormGroup>

        <Row>
          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.employer" />
              </Label>
              <Input
                type="text"
                name="employer"
                value="Employer"
                disabled={!editWorkExperience}
              />
            </FormGroup>
          </Colxx>
          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.role" />
              </Label>
              <Input
                type="text"
                name="role"
                value="Role"
                disabled={!editWorkExperience}
              />
            </FormGroup>
          </Colxx>
        </Row>

        <Row>
          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.from" />
              </Label>
              <Input
                type="text"
                name="from-year"
                value="2021"
                disabled={!editWorkExperience}
              />
            </FormGroup>
          </Colxx>

          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.to" />
              </Label>
              <Input
                type="text"
                name="to-year"
                value="04"
                disabled={!editWorkExperience}
              />
            </FormGroup>
          </Colxx>
        </Row>

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.description" />
          </Label>
          <Input
            type="textarea"
            name="description"
            placeholder="A short description."
            value={experiences}
            rows="6"
            disabled={!editWorkExperience}
          />
        </FormGroup>

        <FormGroup>
          <pre>{JSON.stringify(experiences, null, 2)}</pre>
        </FormGroup>

        {editWorkExperience && (
          <Colxx xxs="12" className="text-center">
            <StateButton
              id="emergencySubmitButton"
              color="primary"
              onClick={handleSuccessButtonClick}
            >
              <IntlMessages id="forms.submit" />
            </StateButton>
          </Colxx>
        )}
      </CardBody>
    </Card>
  );
};

export default WorkExperience;
