import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Button, FormGroup, Row } from 'reactstrap';
import IntlMessages from '../../../../helpers/IntlMessages';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { adminRoot } from '../../../../constants/defaultValues';
import WorkExperience from './WorkExperience';
import UploadResume from './UploadResume';

// const handleSuccessButtonClick = () => {
//   return new Promise((success) => {
//     setTimeout(() => {
//       success('Everything went right!');
//     }, 1400);
//   });
// };

const Experience = ({ match, profileData }) => {
  const [editWorkExperience, toggleEditWorkExperence] = useState(false);
  const [editResume, toggleEditResume] = useState(false);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.experience" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12">
          <UploadResume
            editResume={editResume}
            toggleEditResume={toggleEditResume}
            profileData={profileData}
          />
        </Colxx>
      </Row>

      <Row>
        <Colxx md="6">
          <WorkExperience
            editWorkExperience={editWorkExperience}
            toggleEditWorkExperence={toggleEditWorkExperence}
            profileData={profileData}
          />
        </Colxx>
        <Colxx md="6">
          <img
            src="/assets/img/illustrations/work-experience.png"
            alt="work-experience"
            style={{ width: 'inherit' }}
          />
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              disabled={editWorkExperience || editResume}
              href={`${adminRoot}/profile/education`}
            >
              <IntlMessages id="forms.previous" />
            </Button>

            <Button
              color="primary"
              className="m-3"
              disabled={editWorkExperience || editResume}
              href={`${adminRoot}/profile/links`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ history, authUser }) => {
  const { profileData } = authUser;
  return { profileData, history };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(Experience);
